a{
  text-decoration:none;
}
.container{
  margin:0 auto;
}

.App{
  text-align:center;
}
.main-bg{
  height:300px;
  background-image:url('./bg.png');
  background-position:center;
  background-size:cover;
}
.logo{
  font-size:24px;
  color:#fff;
  margin-right:20px;
  line-height:24px;
}
.navbar-nav > a{
  color:#a7a7a7;
  transition:all 0.3s;
  padding:8px;
}
.navbar-nav > a:hover{
  color:#d7d7d7;
}
.navbar-nav > a:not(:last-child){margin-right:10px;line-height:24px;}

.start{
  opacity : 0;
}
.end{
  opacity:1;
  transition:opacity 0.5s;
}